import { getErrorMessage } from "@rp/common/utils"
import { RpJsonError } from "@rp/react/rpjson/types"
import { IconButton, Typography } from "@rp/react/web/mui/core"
import { CloseRounded } from "@rp/react/web/mui/icons"
// resources
import { DocumentViewerHeader } from "./DocumentViewer.styles"

type Props = {
  error: RpJsonError
  handleClose?: () => void
}

const ErrorDocumentViewer = ({ error, handleClose }: Props) => {
  return (
    <div>
      <DocumentViewerHeader>
        <Typography variant="h4">Error</Typography>
        {handleClose && (
          <IconButton size="small" onClick={handleClose} data-testid="close-icon">
            <CloseRounded />
          </IconButton>
        )}
      </DocumentViewerHeader>
      <div>
        <Typography>{getErrorMessage(error)}</Typography>
      </div>
    </div>
  )
}

export { ErrorDocumentViewer }
